/*--------------------------------------------------------------
1.0 Normalize
Selected elements of normalize.css v3.0.2 by Nicholas Gallagher,
scoped to the gift aid calculator container.
(MIT License - http://git.io/normalize)
--------------------------------------------------------------*/
/**
 * 1. Set default font family to sans-serif.
 */
.giftaid-calculator {
  font-family: sans-serif;
  /* 1 */
  /**
     * Address styling not present in IE 8/9/10/11, Safari, and Chrome.
     */
}
.giftaid-calculator b,
.giftaid-calculator strong {
  font-weight: bold;
}
.giftaid-calculator abbr[title] {
  border-bottom: 1px dotted;
}
.giftaid-calculator input,
.giftaid-calculator select {
  color: inherit;
  font: inherit;
  margin: 0;
}
.giftaid-calculator select {
  text-transform: none;
}
.giftaid-calculator input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
.giftaid-calculator input {
  line-height: normal;
}
.giftaid-calculator input[type="number"]::-webkit-inner-spin-button,
.giftaid-calculator input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
/*--------------------------------------------------------------
2.0 Typography
--------------------------------------------------------------*/
/*--------------------------------------------------------------
3.0 Layout
--------------------------------------------------------------*/
/**
 * 1. Use the simpler box model that includes padding in element heights/widths.
 * 2. Use flexbox layout for the three columns.
 */
.giftaid-calculator {
  box-sizing: border-box;
  display: flex;
}
.giftaid-calculator *,
.giftaid-calculator *:before,
.giftaid-calculator *:after {
  box-sizing: inherit;
}
/* Calculator title. */
/**
 * Giver and Charity columns.
 * 1. Occupy twice as much width as the in-between column.
 */
.giftaid-group {
  flex-grow: 2;
  text-align: center;
}
/**
 * In-between column.
 */
.giftaid-spacing {
  flex-grow: 1;
  text-align: center;
}
/*--------------------------------------------------------------
4.0 Dynamic elements
--------------------------------------------------------------*/
/* Only show the Giver Claims fields to higher-rate taxpayers. */
.giftaid-giver-fields {
  display: none;
}
.show-giver-claims .giftaid-giver-fields {
  display: block;
}
/*--------------------------------------------------------------
5.0 Theme
--------------------------------------------------------------*/
.giftaid-group-giver {
  background: #9cc5bd;
}
.giftaid-group-giver h3 {
  color: #236b5d;
}
.giftaid-group-charity {
  background: #dcf1a1;
}
.giftaid-group-charity h3 {
  color: #5e7914;
}
.giftaid-hmrc {
  background: #f3a2ad;
  color: #791422;
}
